<template>
<div id="car-list">
<van-nav-bar
  left-arrow
  class="car-list-nav"
   @click-left="leftClick"
>
<template #title>
	<font color="white">订单列表</font>
</template>
<template #right>
	<i class="pichecked" @click="showCheckClick">
		<span v-if="piChecked">完成</span>
		<span v-else>批选</span>
	</i>
</template>
</van-nav-bar>
	<car-list-content :car-list="carListImg" :newcreateobj="imgobj"/>
	<van-popup v-model="bottomPopu"  position="bottom" :overlay=false  >
			<i style="position:absolute;color:white;font-style:normal;top:18px;left:18px;">
				{{checkedLen}}/{{carListImg.length}}
			</i>
			<div class="bottomPopu">
				<div class="deleteSelect" @click.stop="deleteClick">删除</div>	
				<div class="allSelect" @click.stop="allSelectClick">
					<span v-if="isAll">全选</span>
					<span v-else>取消</span>
				</div>
			</div>
	</van-popup>
	<i v-show="!bottomPopu" class="addgoods" @click="addgodosClick">+</i>
</div>
	
</template>

<script>
import { NavBar,Popup} from 'vant'
import CarListContent from './childComps/CarListContent'
import {deleteCars} from 'network/car'


export default {
  name: 'CarList',
	components:{
		[NavBar.name]:NavBar,
		[Popup.name]:Popup,
		CarListContent
	},
	data(){
		return{
			showpop:true,
			bottomPopu:false,		//底下弹出框显示
			isAll:true,				//是否全选
		}
	},
	methods:{	
		//返回上级
		leftClick(){
			this.$router.go(-1)
		},

		carClick(){
		
		},
		show(falg){
			this.showpop=falg
		},
		//取消和批选
		showCheckClick(){
			this.bottomPopu=this.$store.state.piChecked=!this.$store.state.piChecked
			//console.log(this.carListImg)
			
		},
		//添加
		addgodosClick(){
			this.$router.push('/recordproduct').catch(err=>err)
		},
		//全选
		allSelectClick(){
			this.isAll=!this.isAll
			//console.log('全选')
			//做一个循环 把里面所有的值都变更
			if(!this.isAll){
				this.carListImg.forEach(item=>item.checked=true)
			}else{
				this.carListImg.forEach(item=>item.checked=false)
			}
		},
		//删除x
		deleteClick(){
			let _self=this
			//把该数组里的所有checked=true的传入一个数组发送到后台删除
			let deteleArr=[];
			this.carListImg.forEach((item,index)=>{
				if(item.checked==true){
					item.index=index
					deteleArr.push(item)
				}
			})
			
			if(deteleArr.length==0){
				this.$toast('没有选中项')
				return false
			}
			//这里来个警告弹窗
			this.$dialog.confirm({
				title: '标题',
				message:
				'是否删除该订单',
			}).then(() => {
				let obj={
					list:deteleArr,
				}
				//开始删除
				deleteCars(obj).then(res=>{
					//删除页面显示的那勾选的数据
					this.$toast.loading({
						message: '删除中...',
						forbidClick: true,
						loadingType: 'spinner',
						onClose(){
							//尼玛的 掂啊
							for(let n=_self.carListImg.length-1;n>=0;n--){
								let item=_self.carListImg[n]
								if(item.checked==true){
									_self.carListImg.splice(n,1)
								}
							}
							_self.$toast(res)
							_self.bottomPopu=false
							_self.$store.state.piChecked=false
						}
					});
				})
			// on confirm
			}).catch(() => {
				// on cancel
				_self.$toast('想起女朋友了？')
			});
		}
	},
	computed:{
		imgobj(){
			return this.$route.params.obj
		},
		piChecked(){
			return this.$store.state.piChecked
		},
		carListImg(){
			return this.$store.state.carListImg
		},
		checkedLen(){
			let deteleArr=[];
			this.carListImg.forEach(item=>{
				if(item.checked==true){
					deteleArr.push(item)
				}
			})
			return deteleArr.length
		}
	},
	mounted(){
		this.$store.state.carListImg=[]
		this.$store.dispatch('getCar')
	}
}
</script>

<style scoped>
*{
	margin:0;
}
#car-list{
    height: 100vh;
    position: relative;
}
.car-list-nav{
	background:#516df5;
	position:relative;
	width:100%;
}
.popup-box{
	border-radius:8px;
	overflow: hidden;
}
/deep/ .van-ellipsis {
	color: white;
}
/deep/ .van-nav-bar__text {
	color: white;
}

/deep/ .van-nav-bar .van-icon {
	color: #ffffff;
}
.pichecked{
	font-style: normal;
	color:white;
	
}
.addgoods{
	position:fixed;
	bottom:50px;
	right:20px;
	z-index:999;
	background:#516df5;
	color:white;
	display: block;
	width:40px;
	height:40px;
	border-radius:40px;
	text-align:center;
	line-height: 40px;
	
}
.bottomPopu{
	background:#3c3c3c;
	overflow: hidden;
	height:49px;
	line-height:49px;
	text-align:right;
}
.bottomPopu div{
	margin-right:20px;
	display:inline-block;
	line-height:24px;
	padding:0px 10px;
	border:1px solid white;
	font-size:13px;
	color:white;
	border-radius: 4px;
}
</style>
