<template>
	<div class="car-list-content">
		<van-list
			v-model="loading"
			:finished="finished"
			finished-text="没有更多了"
			@load="onLoad"
		>
			<div class="content-bxo">
				<car-list-item v-for="(item,index) in CarList" :key="index" :item-obj="item" @click.native="itemClick(item)" @deleteClick="deleteClick(item,index)"/>
			</div>
		</van-list >
		<van-popup v-model="showpop" :style="{ height: '98%',width:'98%'}" class="popup-box" @click="show(false)">
			<p>{{title}}</p>
			<img :src="imgurl" style="width:100%;height:80%;"/>
		</van-popup>
	</div>
		
</template>

<script>
import {List,Popup} from 'vant'
import CarListItem from './CarListItem'
import {deleteCar} from 'network/car'

export default {
  name: 'CarListContent',
  components:{
		[Popup.name]:Popup,
		[List.name]:List,
		CarListItem
  },
  data(){
	return{
		showpop:false,
		list:[],
		loading:false,
		finished :true,
		imgurl:'',
		currendInxdex:0,
		title:'',
	}
  },
  props:{
	CarList:{
		type:Array,
		default(){
			return []
		}
	},
	newcreateobj:{
		type:Object,
		default(){
			return {}
		}
	}
  },
  methods:{
	onLoad(){
	},
	show(falg){
		this.showpop=falg
	},
	itemClick(item){
		this.imgurl=item.url
		this.showpop=true
		this.title=item.createtimee
	},
	//删除一条信息
	deleteClick(item,index){
		let _self=this
		//删除前先来一个弹窗询问：
		this.$dialog.confirm({
				title: '订单',
				message:'您是否删除这张订单？',
			}).then(() => {
				_self.$toast.loading({
					message: '删除中...',
					forbidClick: true,
					loadingType: 'spinner',
				});
				//后台执行
				deleteCar(item).then(res=>{
					setTimeout(()=>{
						//3.再从数组中splice（index，1）的数据 
						this.CarList.splice(index,1)
						_self.$toast(res)
					},1500)
				})
				//1.先删除数据库里的信息
				//2.再删除文档里的文件
			// on confirm
			}).catch(() => {	
			// on cancel
				_self.$toast('取消')
			});

	}
  },
	mounted(){
			//console.log(this.newcreateobj.url)
		if(this.newcreateobj.url!=undefined){
			this.showpop=true
			this.imgurl=this.newcreateobj.url
			this.title=this.newcreateobj.createtime
		}
	},
}
</script>

<style scoped>
	*{
		margin:0;
		padding:0;
	}
.car-list-content{
    position: relative;
	height:calc(100% - 46px);
	background:#f7f7f7;
    right: 0;
    left: 0;
	overflow:auto;
	z-index:9;
}
.car-list-content p{
	text-align:center;
	line-height:45px;
	background-color:#d9f6fd;
}
.content-bxo{
	overflow:hidden;
}
</style>
