		<img src="~assets/images/common/icon/checked.svg" v-else/>
	</i>
	
	<i class="delete-icon" @click.stop="deleteClick" v-else>
			<img src="~assets/images/common/icon/delete2.svg" >
	</i>
	<img :src="ItemObj.url" class="item-img">
	<p>{{ItemObj.createtimee}}</p>
</div>
	
</template>

<script>

export default {
  name: 'CarListItem',
  data(){
	return{
		checked:false,
		deShow:false,
	}
  },
  props:{
	ItemObj:{
		type:Object,
		default(){
			return {}
		}
	}
  },
  methods:{
		deleteClick(){
			this.$emit("deleteClick")
		},
		//改变单个的选取
		//设置一个全局的选中数组
		//这里的数据建议写在vuex里面
		changeClick(){
			this.ItemObj.checked=!this.ItemObj.checked
		}
  },
	computed:{
		piChecked(){
			return this.$store.state.piChecked
		}
	},
}
</script>

